nav {
  height: 65px;
  background-color: $clr3;

  .dark & {
    background-color: $clr6;
    color: $clr2;
  }
  .main-screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(#000, .7);
    display: none;
    cursor: pointer;
    z-index: 5;

    &.open {
      display: block;

      @include for-normal-layout {
        display: none;
      }
    }
  }

  .burger {
    gap: 3px;
    span {
      background-color: $clr1;
      width: 25px;
      height: 4px;
      border-radius: 50px;
      cursor: pointer;

      .dark & {
        background-color: $clr2;
      }
    }

    @include for-normal-layout {
      display: none;
    }
  }

  ul {
    gap: 20px;
    position: fixed;
    width: 200px;
    height: 100%;
    background-color: #fff;
    left: -120%;
    top: 0;
    box-shadow: 0px 0px 10px rgba(#000, .5);
    flex-direction: column;
    z-index: 10;
    transition: left .3s ease-in-out;

    &.open {
      left: 0;
    }

    .dark & {
      color: $clr4;
    }

    img {
      width: 30px;
      height: 30px;
      cursor: pointer;
      transition: all .2s;

      &:hover {
        transform: scale(1.1);
      }
    }


    @include for-normal-layout {
      position: relative;
      left: unset;
      flex-direction: unset;
      background-color: unset;
      box-shadow: unset;
    }

    li {
      margin-inline-end: 10px;
      a {
        color: $clr1;
        .dark & {
          color: $clr1;
        }
        @include for-normal-layout {
          .dark & {
            color: $clr2;
          }
        }
      }
    }
  }

  .logo-container {
    font-family: playball, sans-serif;
    font-size: rem(20px);
    font-weight: 600;

    span {
      color: $clr1;

      .dark & {
        color: $clr2;
      }
    }
  }
}
